const state = {
    open: true,
    moderator: "",
    look: "",
    eventoson: false,
    minimized: true,
};
const namespaced = true;
const mutations = {
    setModerator(state, moderator) {
        state.moderator = moderator;
    },
    setLook(state, look) {
        state.look = look;
    },
    setEvento(state, eventoson) {
        state.eventoson = eventoson;
    },
    setOpen(state, open) {
        state.open = open;
    },
    setMinimized(state, minimized) {
        state.minimized = minimized;
    }
};
export const eventos = {
    namespaced,
    state,
    mutations
};
