import App from '@/App';
export default class EventoEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('evento/setRoomName', data.roomname);
        App.interfaceManager.container.$store.commit('evento/setModerator', data.moderator);
        App.interfaceManager.container.$store.commit('evento/setRoomId', data.roomid);
        App.interfaceManager.container.$store.commit('evento/setLook', data.look);
        App.interfaceManager.container.$store.commit('evento/setType', data.type);
        App.interfaceManager.container.$store.commit('evento/setLink', data.link);
        App.interfaceManager.container.$store.commit('evento/setArgs', data.args);
        App.interfaceManager.container.$store.commit('evento/setOpen', true);
    }
}
