import App from '@/App';
export default class UserClickEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('userclick/setIsMe', data.isme);
        App.interfaceManager.container.$store.commit('userclick/setName', data.username);
        App.interfaceManager.container.$store.commit('userclick/setId', data.userid);
        App.interfaceManager.container.$store.commit('userclick/setLook', data.look);
        App.interfaceManager.container.$store.commit('userclick/setMinimized', data.minimized);
        App.interfaceManager.container.$store.commit('userclick/setOpen', true);
    }
}
