import App from '@/App';
export default class EventosEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('eventos/setEvento', data.eventoson);
        App.interfaceManager.container.$store.commit('eventos/setModerator', data.moderator);
        App.interfaceManager.container.$store.commit('eventos/setLook', data.look);
        App.interfaceManager.container.$store.commit('eventos/setMinimized', data.minimized);
        App.interfaceManager.container.$store.commit('eventos/setOpen', true);
    }
}
