import Logger from '@/utils/Logger';
import MoveAvatarComposer from '@/communication/outgoing/general/MoveAvatarComposer';
import App from '@/App';
export default class MovementInteraction {
    _walking;
    _walkInterval;
    bool;
    constructor() {
        console.log("Iniciei o movimento");
        this.bool = false;
        this._walking = false;
        this._walkInterval = null;
        let frame = document.getElementById('nitro');
        frame.contentWindow.addEventListener("keydown", (event) => {
            this.onKeyDown(event);
        });
    }
    onKeyDown(e) {
        let enabled = true;
        if (enabled) {
            this._walking = true;
            clearTimeout(this._walkInterval);
            let direction = "";
            switch (e.keyCode) {
                case 37:
                    direction = "left";
                    break;
                case 38:
                    direction = "up";
                    break;
                case 39:
                    direction = "right";
                    break;
                case 40:
                    direction = "down";
                    break;
                default:
                    return;
            }
            Logger.Log(direction);
            App.communicationManager.sendMessage(new MoveAvatarComposer(direction));
        }
    }
    onKeyUp(e) {
        console.log("up");
    }
}
