import App from '@/App';
export default class YoutubeTVEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('youtubeplayer/setOpen', true);
        App.interfaceManager.container.$store.commit('youtubeplayer/setVideoId', data.videoId);
        App.interfaceManager.container.$store.commit('youtubeplayer/setLook', data.look);
        App.interfaceManager.container.$store.commit('youtubeplayer/setNickname', data.nickname);
        App.interfaceManager.container.$store.commit('youtubeplayer/setDuration', data.duration);
        App.interfaceManager.container.$store.commit('youtubeplayer/setVideoname', data.videoname);
        App.interfaceManager.container.$store.commit('youtubeplayer/setTest', data.test);
        App.interfaceManager.container.$store.commit('youtubeplayer/setItemId', data.itemId);
        App.interfaceManager.container.$store.commit('youtubeplayer/setEditMode', data.videoId == "");
    }
}
