import App from '@/App';
export default class BattlePassEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('battlepasscmp/setDiamonds', data.diamonds);
        App.interfaceManager.container.$store.commit('battlepasscmp/setCredits', data.credits);
        App.interfaceManager.container.$store.commit('battlepasscmp/setLevel', data.level);
        App.interfaceManager.container.$store.commit('battlepasscmp/setImagem', data.imagem);
        App.interfaceManager.container.$store.commit('battlepasscmp/setOpen', true);
    }
}
