const state = {
    open: false,
    moderator: "",
    message: "",
    look: "",
    roomname: "",
};
const namespaced = true;
const mutations = {
    setModerator(state, moderator) {
        state.moderator = moderator;
    },
    setMessage(state, message) {
        state.message = message;
    },
    setLook(state, look) {
        state.look = look;
    },
    setRoomname(state, roomname) {
        state.roomname = roomname;
    },
    setOpen(state, open) {
        state.open = open;
    }
};
export const explicar = {
    namespaced,
    state,
    mutations
};
