import App from '@/App';
export default class ExplicarEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('explicar/setMessage', data.message);
        App.interfaceManager.container.$store.commit('explicar/setModerator', data.moderator);
        App.interfaceManager.container.$store.commit('explicar/setLook', data.look);
        App.interfaceManager.container.$store.commit('explicar/setRoomname', data.roomname);
        App.interfaceManager.container.$store.commit('explicar/setOpen', true);
    }
}
