export default class AnotherEventComposer {
    header = "anevent";
    data;
    constructor(roomname, look, moderator, roomid, type, link, args) {
        this.data = {
            "roomname": roomname,
            "look": look,
            "moderator": moderator,
            "roomid": roomid,
            "type": type,
            "link": link,
            "args": args,
        };
    }
}
