const state = {
    level: [],
    credits: [],
    diamonds: [],
    imagem: [],
    open: false,
};
const namespaced = true;
const mutations = {
    setOpen(state, open) {
        state.open = open;
    },
    setLevel(state, level) {
        state.level = level;
    },
    setDiamonds(state, diamonds) {
        state.diamonds = diamonds;
    },
    setCredits(state, credits) {
        state.credits = credits;
    },
    setImagem(state, imagem) {
        state.imagem = imagem;
    },
};
export const battlepasscmp = {
    namespaced,
    state,
    mutations
};
