import Vue from 'vue';
import Vuex from 'vuex';
import { session } from './modules/session';
import { commands } from './modules/commands';
import { jukebox } from './modules/jukebox';
import { slotmachine } from './modules/slotmachine';
import { youtubeplayer } from './modules/youtubeplayer';
import { youtubebool } from './modules/youtubebool';
import { twitchplayer } from './modules/twitchplayer';
import { evento } from './modules/evento';
import { eventos } from './modules/eventos';
import { explicar } from './modules/explicar';
import { battlepass } from './modules/battlepass';
import { userclick } from './modules/userclick';
import { battlepasscmp } from './modules/battlepasscmp';
Vue.use(Vuex);
const store = {
    state: {
        connected: false
    },
    modules: {
        session,
        userclick,
        commands,
        jukebox,
        slotmachine,
        youtubeplayer,
        youtubebool,
        twitchplayer,
        evento,
        eventos,
        explicar,
        battlepass,
        battlepasscmp
    },
    mutations: {
        setConnected(state, val) {
            state.connected = val;
        }
    }
};
export default new Vuex.Store(store);
