const state = {
    username: "",
    userid: 0,
    minimized: false,
    look: "",
    isme: false,
    open: false,
};
const namespaced = true;
const mutations = {
    setName(state, username) {
        state.username = username;
    },
    setLook(state, look) {
        state.look = look;
    },
    setMinimized(state, minimized) {
        state.minimized = minimized;
    },
    setId(state, userid) {
        state.userid = userid;
    },
    setIsMe(state, isme) {
        state.isme = isme;
    },
    setOpen(state, open) {
        state.open = open;
    }
};
export const userclick = {
    namespaced,
    state,
    mutations
};
