const state = {
    open: false,
    moderator: "",
    roomname: "",
    roomid: 0,
    type: "",
    look: "",
    link: "",
    args: "",
};
const namespaced = true;
const mutations = {
    setModerator(state, moderator) {
        state.moderator = moderator;
    },
    setRoomName(state, roomname) {
        state.roomname = roomname;
    },
    setRoomId(state, roomid) {
        state.roomid = roomid;
    },
    setLook(state, look) {
        state.look = look;
    },
    setType(state, type) {
        state.type = type;
    },
    setLink(state, link) {
        state.link = link;
    },
    setArgs(state, args) {
        state.args = args;
    },
    setOpen(state, open) {
        state.open = open;
    }
};
export const evento = {
    namespaced,
    state,
    mutations
};
